.landing-page{
    width: 100%;
    margin: 0 auto;
    background-color: rgba(51, 51 ,51, 0.3);  
    padding: 10px 0 100px 0;
    color: white;
}

.landing-page > h2 {
    text-align: center;
    color: white;
}

.landing-page-header{
    width: 800px;
    text-align: center;
    padding: 10px;
    display: flex;
    margin: 0 auto;
}

h1 {
    color: white;
    text-align: center;
    font-size: 36px;
}

.left-header{
    width: 100%;
    margin-top: 110px;
}

.left-header > p {
    font-size: 24px;
}

.right-header{
    width: 50%;
}

.landing-page-section {
    display: flex;
    margin: 0 auto;
    max-width: 800px;
}

.landing-page-footer{
    margin: 50px auto 0 auto;
    text-align: center;
}

