body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background-color: black; */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.dashboard {
    width: 100%;
}

.dashboard-header{
    padding: 10px;
}

.dashboard-section {
    display: flex;
    flex-wrap: wrap;
}

p {
    font-size: 18px;
    text-align: center;
}

.ant-card > ul {
    display: block;
}

.hover-effect {
    -webkit-transition: -webkit-transform .1s;
    transition: -webkit-transform .1s;
    transition: transform .1s;
    transition: transform .1s, -webkit-transform .1s;
    -webkit-transition: transform .1;
    -moz-transition: transform 0.1;
    -o-transition: transform 0.1;
}

.hover-effect:hover{
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}

/* Anything < 640px, the code applies */
@media only screen and (max-width: 640px){
    .mid-section {
        display: block;
    }
    .banner-header-progress-dashboard {
        display: none;
    }

    .banner-header-progress-line {
        display: block;
        padding-top: 15px;
        padding-right: 50px;
    }
}

.userboard {
    padding: 5px;
    width: 100%;
    /* border: black 1px solid; */
}

.banner {
    width: 100%;
    background: rgba(255,255,255,0.9);
    border: 2px solid white;
    border-radius: 5px;
    color: rgb(51,51,51);
}

/* background: rgb(255,255,255, 0.3); */

.banner-header {
    padding: 20px;
    position: relative;
}

.banner-header-avatar {
    display: inline-block;
    padding-right: 10px;
}

.banner-header-user {
    display: inline-block;
    position: absolute;
    left: 140px;
    top: 20px;
}

.banner-header-user > h1 {
    padding: 0px;
    font-size: 18px;
    text-align: left;
    color: rgb(51,51,51);
}

.banner-header-user > p {
    padding: 0px;
    font-size: 14px;
    text-align: left;
}

.banner-header-progress-dashboard {
    display: inline-block;
    float: right;
}

.banner-header-progress-line {
    display: none;
}

.banner-footer {
    margin-left: 20px;
    margin-bottom: 10px;
}

.social-media-links {
    color: rgb(51,51,51);
}
.section {
    color: rgb(51,51,51);
    width: 100%;
}

.section-header {
    background: rgba(255,255,255,0.9);
    border: 2px solid white;
    width: 100%;
    border-radius: 5px;
    padding: 10px;
    margin-top: 10px;
}

.section-header-content {
    margin-left: 25px;
}


.mid-section {
    display: flex;
    margin-top: 10px;
}

.section-left {
    padding: 10px 0 10px 40px;
    margin-right: 10px;
    flex: 1 1;
    background: rgba(255,255,255,0.9);
    border: 2px solid white;
    border-radius: 5px;
}

.section-right {
    padding: 10px 55px 10px 55px;
    flex: 1 1;
    background: rgba(255,255,255,0.9);
    border: 2px solid white;
    border-radius: 5px;
}

.section-left > h1, .section-right > h1, .section-header > h1 {
    color: rgb(51,51,51);
    margin-left: 25px;
    display: inline-block;
    font-size: 18px;
    font-weight: bold;
}

.quote {
    font-size: 18px;
}

.ant-card-head{
    padding: 0 12px;
}

.ant-card-body {
    padding: 20px 10px;
}

.font-color {
    color: rgb(51,51,51);
}

