    color: white;
}

.landing-page-header{
    width: 800px;
    text-align: center;
    padding: 10px;
    display: flex;
    margin: 0 auto;
}

h1 {
    color: white;
    text-align: center;
    font-size: 36px;
}

.left-header{
    width: 100%;
    margin-top: 110px;
}

.left-header > p {
